        this.projectsScrollStep;
      document.getElementById(this.projectsEl).scrollLeft = scrollVal;
    },
    projectsLeft() {
      const scrollVal =
        document.getElementById(this.projectsEl).scrollLeft -
        this.projectsScrollStep;
      document.getElementById(this.projectsEl).scrollLeft = scrollVal;
    },
    closeModal() {
      this.modalOpen = false;
    },
    goToView(view) {
      const yOffset = -60;
      const element = document.getElementById(view);
      if (element) {
        const y =
          element.getBoundingClientRect().top + window.pageYOffset + yOffset;

        window.scrollTo({ top: y, behavior: "smooth" });
      }
    },
    order() {
      if (this.name && this.phone) {
        this.openedPlan = null;
        Email.send({
          SecureToken: "bd3a71ae-95c4-46ae-8389-a802abc8a2c1",
          To: this.emaiToSend,
          From: "tastai.landing@gmail.com",
          Subject: "Новая заявка с сайта Tastai Development",
          Body: `Поступила новая заявка. Имя: ${this.name}, телефон: ${this.phone}.`,
        }).then(() => (this.modalOpen = true));
      }
    },
    changeActiveProjectGroup(index) {
